
















import {Component, Prop, Vue} from "vue-property-decorator"
import {IPublicTrainer} from "@chaosinsight/yogipeople-portalclient"

@Component({ components: { } })
export default class ListTeachers extends Vue {
	@Prop()
	public title!: string

	@Prop()
	public trainers!: IPublicTrainer[]

	@Prop()
	private selectedTrainerId!: string

	private trainerData(trainer: IPublicTrainer): void {
		this.$emit("trainerData", trainer)
	}
}
