






















import {Component, Prop, Vue} from "vue-property-decorator"
import {IBooking, IPublicTrainer, ITrainerProfile} from "@chaosinsight/yogipeople-portalclient"
import Portal from "@/managers/portal"
import ListTeachers from "@/components/ListTeachers.vue"
import ListUpcoming from "@/components/ListUpcoming.vue"

@Component({ components: { ListTeachers, ListUpcoming } })
export default class StudentBookings extends Vue {
	@Prop()
	private selectedBookingId!: number

	@Prop()
	private selectedTrainerId!: string

	@Prop()
	private canceledBookingId!: number

	private trainers: IPublicTrainer[] | null = null
	private bookings: IBooking[] | null = null

	private listenInterval = 5000
	private listenHandle: number | null = null
	private clientTimeDelta: number = 0

	private created(): void {
		Portal.client.call.system.Time()
			.response
			.then(res => this.clientTimeDelta = new Date(res.Time).getTime() - Date.now() )
	}

	private mounted(): void {
		this.listenBookingsAndTrainers()
	}

	private destroyed(): void {
		if (this.listenHandle !== null)
			clearTimeout(this.listenHandle)
	}

	/**
	 * Listen Upcoming bookings
	 */

	private listenBookingsAndTrainers(): void {
		this.getBookings()
			.then(() => {
				this.getTrainers()
					.then(() => {
						this.listenHandle = window.setTimeout(() => this.listenBookingsAndTrainers(), this.listenInterval)
					})
			})
	}

	private getBookings(): Promise<void> {
		return Portal.client.call.booking.get()
			.response
			.then(result => {
				this.bookings = result.filter(el => !el.IsCancelled && !el.IsUsed && new Date(el.TimeSlot.End).getTime() > (Date.now() + this.clientTimeDelta))
				if (this.bookings.length) {
					this.bookings = this.bookings.sort((a, b) => a.TimeSlot.Start > b.TimeSlot.Start ? 1 : -1)
					this.checkNextBooking(this.bookings)
					this.$emit("listUpcoming", true)
				} else
					this.$emit("listUpcoming", false)
			})
	}

	/**
	 * Get My Trainers list
	 */

	private getTrainers(): Promise<void> {
		return Portal.client.call.student.follows()
			.response
			.then(result => {
				this.trainers = result
				if (this.trainers.length)
					this.$emit("listTrainers", true)
				else
					this.$emit("listTrainers", false)
			})
	}

	// Check next upcoming booking
	private checkNextBooking(bookings: IBooking[]) {
		const interval = new Date(bookings[0].TimeSlot.Start).getTime() - (Date.now() + this.clientTimeDelta)
		if (interval < 120 * 1000)
			this.$emit("timeUntilBooking", bookings[0])
	}

	/**
	 * Listen $emit from "ListUpcoming" and $emit to "StudentLanding"
	 */

	private timeUntilBooking(booking: IBooking) {
		this.$emit("timeUntilBooking", booking)
	}

	/**
	 * Listen $emit from "ListTeachers" and $emit to "StudentLanding"
	 */

	private trainerData(trainer: ITrainerProfile): void {
		this.$emit("trainerData", trainer)
	}
}
